*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: lightblue;
}

.boxTarefas{
  width: 80%;
  max-width: 800px;
  background-color: white;
  padding: 30px 50px;
  margin: 10px auto;
  border-radius: 10px;
}

.boxTarefas h2{
  text-align: center;
  margin-bottom: 30px;
}

.tarefaSingle{
  font-size: 15px;
  padding: 8px 10px;
  border-bottom: 2px dotted #ccc;
  display: flex;
}
.tarefaSingle p{
  cursor: pointer;
  width: 80%;
}
.tarefaSingle span{
  cursor: pointer;
  color: red;
  width: 50px;
}

.addTarefa{
  position: fixed;
  right: 10px;
  bottom: 10px;
  background-color: lightcoral;
  width: 50px;
  height: 50px;
  cursor: pointer;
  border-radius:50%;
  color: white;
  text-align: center;
  line-height: 50px;

}

.modal{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.modalContent{
  max-width: 600px;
  padding: 40px 20px;
  background-color: white;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
